import Plotly from 'plotly.js-dist-min';

// 
export function draw_lineplot(id, result, tag) {
  // 
  let title = "";
  if (tag == "f1") {
    title = "";
  }
  if (tag == "f2") {
    title = "";
  }
  if (tag == "g") {
    title = "";
  }
  // 
  const y_test = {
    x: Object.keys(result[tag].y_test),
    y: result[tag].y_test,
    type: 'lines+markers',
    name: ""
  };
  const y_pred = {
    x: Object.keys(result[tag].y_pred),
    y: result[tag].y_pred,
    type: 'lines+markers',
    name: ""
  };
  const data = [y_test, y_pred];
  const layout = {
    title
    // xaxis: {
    //   title: ''
    // }
  };
  const config = { responsive: true };
  Plotly.newPlot(id, data, layout, config);
}

// 
export function draw_scatterplot(id, data, title = "") {
  const y = {
    x: Object.keys(data),
    y: data,
    type: 'scatter',
  };
  const layout = {
    title
  };
  const config = { responsive: true };
  Plotly.newPlot(id, [y], layout, config);
}
