<template>
  <div id="train-algorithm-lr">
    <el-card shadow="hover">
      <div slot="header">
        <b></b>
      </div>
      <div class="setting">
        <div class="setting-method" style="margin-right: 20px;">
          <span></span>
          <el-input-number v-model="train_algorithm_lr_setting.test_size" :min="0.05" :max="0.3"
            :step="0.01"></el-input-number>
        </div>
        <!-- 0 -->
        <!-- <div class="setting-method" style="margin-right: 20px;">
          <span>0</span>
          <el-select v-model="train_algorithm_lr_setting.paras[0].fit_intercept" placeholder="0">
            <el-option v-for="method in fit_intercept_state" :key="method.value" :label="method.label"
              :value="method.value">
            </el-option>
          </el-select>
        </div> -->
      </div>
    </el-card>
    <el-card class="info" shadow="hover">
      <div slot="header">
        <b></b>
      </div>
      <p><b></b>0.050.3</p>
    </el-card>
    <el-card class="info" shadow="hover">
      <div slot="header">
        <b></b>
      </div>
      <p>
        
      </p>
      <p>
        <Latex>y_{t}=m+n x_{t}</Latex>
      </p>
      <p>
        x(t)t
      </p>
      <p>
        mn
      </p>
      <p>
        
      </p>
      <p>
        <Latex>y=p_{0}+p_{1} x_{1}+p_{2} x_{2}+\cdots+p_{k} x_{k}+e</Latex>
      </p>
      <p>
        p(0)
      </p>
      <p>
        <Latex>p_{1}p_{2}\cdots p_{k}</Latex>
      </p>
      <p>
        
      </p>
    </el-card>
    <el-card class="form-upload" shadow="hover">
      <div slot="header">
        <div class="model-tool">
          <el-button type="primary" @click="train_model"></el-button>
          <el-button type="primary" @click="save"></el-button>
        </div>
      </div>
      <div v-show="Object.keys(train_algorithm_lr_result).length > 0" class="model-result">
        <!--  -->
        <!--  -->
        <!--  -->
        <div class="formula">
          <Latex>{{ f1_katex }}</Latex>
        </div>
        <div class="formula">
          <Latex>{{ f2_katex }}</Latex>
        </div>
        <!--  -->
        <b>
          
        </b>
        <el-table v-if="Object.keys(train_algorithm_lr_result).length > 0" :data="[train_algorithm_lr_result.f1.error]"
          style="width: 100%;margin-bottom: 30px;" stripe>
          <el-table-column v-for="column in error_columns" :key="column" :prop="column" :label="column">
          </el-table-column>
        </el-table>
        <b>
          
        </b>
        <el-table v-if="Object.keys(train_algorithm_lr_result).length > 0" :data="[train_algorithm_lr_result.f2.error]"
          style="width: 100%;margin-bottom: 30px;" stripe>
          <el-table-column v-for="column in error_columns" :key="column" :prop="column" :label="column">
          </el-table-column>
        </el-table>
        <!--  -->
        <div id="lr-f1-lineplot" style="width: 100%;"></div>
        <div id="lr-f2-lineplot"></div>
        <div id="lr-g-lineplot"></div>
      </div>
    </el-card>
  </div>
</template>

<script>
// import * as XLSX from 'xlsx';
import { train } from "@/api";
import Latex from "@/components/Latex.vue";
import { draw_lineplot } from "@/utils/draw";


export default {
  name: 'TrainAlgorithmLr',
  components: {
    Latex
  },
  data() {
    return {
      // 
      fit_intercept_state: [{
        value: "true",
        label: ''
      }, {
        value: "false",
        label: ''
      }],
      // 
      columns: ["SPO", "BMPO", "BFPO", "SPE", "BMPE", "BFPE", "ST", "BT", "WP", "FN", "SFR", "BFR"],
      error_columns: ["MAE", "RMSE", "R2"],
    };
  },
  computed: {
    train_process_result: { // 
      get() {
        return this.$store.state.train_process_result;
      },
      set(newVal) {
        this.$store.commit('set_train_process_result', newVal);
      }
    },
    train_algorithm_lr_setting: { // 
      get() {
        return this.$store.state.train_algorithm_lr_setting;
      },
      set(newVal) {
        this.$store.commit('set_train_algorithm_lr_setting', newVal);
      }
    },
    train_algorithm_lr_result: { // 
      get() {
        return this.$store.state.train_algorithm_lr_result;
      },
      set(newVal) {
        this.$store.commit('set_train_algorithm_lr_result', newVal);
      }
    },
    train_save: { // 
      get() {
        return this.$store.state.train_save;
      },
      set(newVal) {
        this.$store.commit('set_train_save', newVal);
      }
    },
    // 
    f1_katex() {
      const train_algorithm_lr_result = this.$store.state.train_algorithm_lr_result;
      if (Object.keys(train_algorithm_lr_result).length > 0) {
        let coef = train_algorithm_lr_result.f1.results.coef;
        let intercept = train_algorithm_lr_result.f1.results.intercept;
        // latex
        coef = coef.map((v, i) => i == 0 ? v : (v < 0 ? "" : "+") + v); // +
        intercept = (intercept < 0 ? "" : "+") + intercept;

        return "f1=" + coef.map((v, i) => v + "\\times " + this.columns[i]).join("") + intercept;
      }
    },
    f2_katex() {
      const train_algorithm_lr_result = this.$store.state.train_algorithm_lr_result;
      if (Object.keys(train_algorithm_lr_result).length > 0) {
        let coef = train_algorithm_lr_result.f2.results.coef;
        let intercept = train_algorithm_lr_result.f2.results.intercept;
        // latex
        coef = coef.map((v, i) => i == 0 ? v : (v < 0 ? "" : "+") + v); // +
        intercept = (intercept < 0 ? "" : "+") + intercept;

        return "f2=" + coef.map((v, i) => v + "\\times " + this.columns[i]).join("") + intercept;
      }
    },
  },
  watch: {
    // train_algorithm_lr_setting
    train_algorithm_lr_setting: {
      handler(newVal, oldVal) {
        this.train_algorithm_lr_setting = newVal;
      },
      deep: true,
      immediate: true
    },
  },
  methods: {
    // 
    train_model() {
      // 
      if (this.train_process_result.length > 0) {
        // 
        this.train_algorithm_lr_setting.data = this.train_process_result;
        train(this.train_algorithm_lr_setting).then(result => {
          // console.log(result);
          this.train_algorithm_lr_result = result;
          // 
          this.draw_lineplot_all();
        });
      } else {
        this.$message({
          showClose: true,
          message: '',
          type: 'error'
        });
      }
    },
    draw_lineplot_all() {
      // 
      draw_lineplot("lr-f1-lineplot", this.train_algorithm_lr_result, "f1");
      draw_lineplot("lr-f2-lineplot", this.train_algorithm_lr_result, "f2");
      // draw_lineplot("lr-g-lineplot", this.train_algorithm_lr_result, "g");
    },
    // 
    save() {
      // 
      if (Object.keys(this.train_algorithm_lr_result).length > 0) {
        const train_algorithm_lr_setting = this.train_algorithm_lr_setting;
        const train_algorithm_lr_result = this.train_algorithm_lr_result;
        this.train_save = {
          train_algorithm_setting: train_algorithm_lr_setting,
          train_algorithm_result: train_algorithm_lr_result
        };
        // 
        this.$message({
          message: '',
          type: 'success'
        });
      } else {
        this.$message({
          showClose: true,
          message: '',
          type: 'error'
        });
      }
    },
  },
  mounted() {
    // 
    if (Object.keys(this.train_algorithm_lr_result).length > 0) {
      this.draw_lineplot_all();
    }
  }
};
</script>

<style lang="scss" scoped>
#train-algorithm-lr {

  // card
  &>* {
    margin-bottom: 20px;
  }

  .setting {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;

    .setting-method {
      margin-bottom: 20px;
    }
  }
}
</style>
