<template>
  <div id="train-algorithm-bpn">
    <el-card shadow="hover">
      <div slot="header">
        <b></b>
      </div>
      <div class="setting">
        <!--  -->
        <div class="setting-method" style="margin-right: 20px;">
          <span></span>
          <el-input-number v-model="train_algorithm_bpn_setting.test_size" :min="0.05" :max="0.3"
            :step="0.01"></el-input-number>
        </div>
        <!-- hidden_layer_sizes_1 -->
        <div class="setting-method" style="margin-right: 20px;">
          <span>1</span>
          <el-input-number v-model="train_algorithm_bpn_setting.paras[0].hidden_layer_sizes_1" :min="1"
            :step="1"></el-input-number>
        </div>
        <!-- hidden_layer_sizes_2 -->
        <div class="setting-method" style="margin-right: 20px;">
          <span>2</span>
          <el-input-number v-model="train_algorithm_bpn_setting.paras[0].hidden_layer_sizes_2" :min="1"
            :step="1"></el-input-number>
        </div>
        <!-- random_state -->
        <div class="setting-method" style="margin-right: 20px;">
          <span></span>
          <el-input-number v-model="train_algorithm_bpn_setting.paras[0].random_state" :min="0"
            :step="1"></el-input-number>
        </div>
        <!-- activation -->
        <div class="setting-method" style="margin-right: 20px;">
          <span></span>
          <el-select v-model="train_algorithm_bpn_setting.paras[0].activation" placeholder="activation">
            <el-option v-for="activation in activations" :key="activation.value" :label="activation.label"
              :value="activation.value">
            </el-option>
          </el-select>
        </div>
      </div>
    </el-card>
    <el-card class="info" shadow="hover">
      <div slot="header">
        <b></b>
      </div>
      <p><b></b>0.050.3</p>
      <p><b>1</b>11</p>
      <p><b>2</b>21</p>
      <p><b></b>0</p>
      <p><b></b></p>
      <p>
        identityf(x) = x
      </p>
      <p>
        logisticsigmoidf(x) = 1 / (1 + exp(-x))
      </p>
      <p>
        tanhtanf(x) = tanh(x)
      </p>
      <p>
        reluf(x) = max(0, x)
      </p>
    </el-card>
    <el-card class="info" shadow="hover">
      <div slot="header">
        <b></b>
      </div>
      <p>
        BPnqmvwF1SF2
      </p>
      <el-image class="image" :src="require('@/assets/imgs/train_bpn_1.png')"
        :preview-src-list="[require('@/assets/imgs/train_bpn_1.png')]" fit="contain">
      </el-image>
      <p>
        BP;, , ,
        , , BPnm
      </p>
    </el-card>
    <el-card class="form-upload" shadow="hover">
      <div slot="header">
        <div class="model-tool">
          <el-button type="primary" @click="train_model"></el-button>
          <el-button type="primary" @click="save"></el-button>
        </div>
      </div>
      <div v-show="Object.keys(train_algorithm_bpn_result).length > 0" class="model-result">
        <!--  -->
        <b>
          
        </b>
        <el-table v-if="Object.keys(train_algorithm_bpn_result).length > 0" :data="[train_algorithm_bpn_result.f1.error]"
          style="width: 100%;margin-bottom: 30px;" stripe>
          <el-table-column v-for="column in error_columns" :key="column" :prop="column" :label="column">
          </el-table-column>
        </el-table>
        <b>
          
        </b>
        <el-table v-if="Object.keys(train_algorithm_bpn_result).length > 0" :data="[train_algorithm_bpn_result.f2.error]"
          style="width: 100%;margin-bottom: 30px;" stripe>
          <el-table-column v-for="column in error_columns" :key="column" :prop="column" :label="column">
          </el-table-column>
        </el-table>
        <!--  -->
        <div id="lr-f1-lineplot"></div>
        <div id="lr-f2-lineplot"></div>
        <div id="lr-g-lineplot"></div>
      </div>
    </el-card>
  </div>
</template>

<script>
// import * as XLSX from 'xlsx';
import { train } from "@/api";
import { draw_lineplot } from "@/utils/draw";


export default {
  name: 'TrainAlgorithmBpn',
  data() {
    return {
      // activation
      activations: [{
        value: "identity",
        label: 'identity'
      },
      {
        value: "logistic",
        label: 'logistic'
      },
      {
        value: "tanh",
        label: 'tanh'
      },
      {
        value: "relu",
        label: 'relu'
      },
      ],
      // 
      error_columns: ["MAE", "RMSE", "R2"],
    };
  },
  computed: {
    train_process_result: { // 
      get() {
        return this.$store.state.train_process_result;
      },
      set(newVal) {
        this.$store.commit('set_train_process_result', newVal);
      }
    },
    train_algorithm_bpn_setting: { // BP
      get() {
        return this.$store.state.train_algorithm_bpn_setting;
      },
      set(newVal) {
        this.$store.commit('set_train_algorithm_bpn_setting', newVal);
      }
    },
    train_algorithm_bpn_result: { // BP
      get() {
        return this.$store.state.train_algorithm_bpn_result;
      },
      set(newVal) {
        this.$store.commit('set_train_algorithm_bpn_result', newVal);
      }
    },
    train_save: { // 
      get() {
        return this.$store.state.train_save;
      },
      set(newVal) {
        this.$store.commit('set_train_save', newVal);
      }
    },
  },
  watch: {
    // train_algorithm_bpn_setting
    train_algorithm_bpn_setting: {
      handler(newVal, oldVal) {
        this.train_algorithm_bpn_setting = newVal;
      },
      deep: true,
      immediate: true
    },
  },
  methods: {
    // 
    train_model() {
      // 
      if (this.train_process_result.length > 0) {
        // 
        this.train_algorithm_bpn_setting.data = this.train_process_result;
        train(this.train_algorithm_bpn_setting).then(result => {
          this.train_algorithm_bpn_result = result;
          // 
          this.draw_lineplot_all();
        });
      } else {
        this.$message({
          showClose: true,
          message: '',
          type: 'error'
        });
      }
    },
    // 
    save() {
      // 
      if (Object.keys(this.train_algorithm_bpn_result).length > 0) {
        const train_algorithm_bpn_setting = this.train_algorithm_bpn_setting;
        const train_algorithm_bpn_result = this.train_algorithm_bpn_result;
        this.train_save = {
          train_algorithm_setting: train_algorithm_bpn_setting,
          train_algorithm_result: train_algorithm_bpn_result
        };
        // 
        this.$message({
          message: '',
          type: 'success'
        });
      } else {
        this.$message({
          showClose: true,
          message: '',
          type: 'error'
        });
      }
    },
    draw_lineplot_all() {
      // 
      draw_lineplot("lr-f1-lineplot", this.train_algorithm_bpn_result, "f1");
      draw_lineplot("lr-f2-lineplot", this.train_algorithm_bpn_result, "f2");
      // draw_lineplot("lr-g-lineplot", this.train_algorithm_bpn_result, "g");
    },
  },
  mounted() {
    // 
    if (Object.keys(this.train_algorithm_bpn_result).length > 0) {
      this.draw_lineplot_all();
    }
  }
};
</script>

<style lang="scss" scoped>
#train-algorithm-bpn {

  // card
  &>* {
    margin-bottom: 20px;
  }

  .setting {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;

    .setting-method {
      margin-bottom: 20px;
    }
  }

  // .model-tool {
  //   display: flex;
  //   flex-direction: row;
  //   justify-content: space-between;
  //   align-items: center;
  // }
}
</style>
