<template>
  <div id="process-result">
    <el-card class="info" shadow="hover">
      <div slot="header">
        <el-button type="primary" @click="process"></el-button>
      </div>
      <!--  -->
      <el-table v-if="process_result.length > 0"
        :data="process_result.slice((currentpage - 1) * pagesize, currentpage * pagesize)" :height="300"
        style="width: 100%" stripe>
        <!--  -->
        <el-table-column prop="" label="">
          <el-table-column prop="" label="" fixed>
            <el-table-column type="index" width="50" :index="table_index" fixed>
            </el-table-column>
          </el-table-column>
        </el-table-column>
        <!--  -->
        <el-table-column v-for="column in columns" :key="column" :prop="column" :label="labels[column].meaning">
          <el-table-column :prop="column" :label="column">
            <!--  -->
            <el-table-column v-if="!process_setting.standard[0].state" :prop="column" :label="labels[column].unit">
            </el-table-column>
          </el-table-column>
        </el-table-column>
        <!-- <el-table-column type="index" width="50" fixed>
        </el-table-column>
        <el-table-column v-for="column in columns" :key="column" :prop="column" :label="column">
        </el-table-column> -->
      </el-table>
      <!--  -->
      <div>
        <el-pagination v-if="process_result.length > 0" align='center' @size-change="handleSizeChange"
          @current-change="handleCurrentChange" :current-page="currentpage" :page-sizes="[5, 10, 50, 100]"
          :page-size="pagesize" layout="total, sizes, prev, pager, next, jumper" :total="process_input_data.length">
        </el-pagination>
      </div>
    </el-card>
  </div>
</template>

<script>
import { process_user } from "@/api";
import * as d3 from "d3-dsv";
import axios from "axios";

export default {
  name: 'ProcessResult',
  data() {
    return {
      // 
      columns: ["SPO", "BMPO", "BFPO", "SPE", "BMPE", "BFPE", "ST", "BT", "WP", "FN", "SFR", "BFR"],
      // 
      labels: {
        "SPO": {
          meaning: "",
          unit: ""
        },
        "BMPO": {
          meaning: "",
          unit: ""
        },
        "BFPO": {
          meaning: "",
          unit: ""
        },
        "SPE": {
          meaning: "",
          unit: "mD"
        },
        "BMPE": {
          meaning: "",
          unit: "mD"
        },
        "BFPE": {
          meaning: "",
          unit: "D"
        },
        "ST": {
          meaning: "",
          unit: ""
        },
        "BT": {
          meaning: "",
          unit: "m"
        },
        "WP": {
          meaning: "",
          unit: "m"
        },
        "FN": {
          meaning: "",
          unit: ""
        },
        "SFR": {
          meaning: "",
          unit: "m/s"
        },
        "BFR": {
          meaning: "",
          unit: "m/s"
        },
        "SD": {
          meaning: "",
          unit: ""
        },
        "FP": {
          meaning: "",
          unit: ""
        },
        "f1": {
          meaning: "",
          unit: ""
        },
        "f2": {
          meaning: "",
          unit: ""
        },
        "g": {
          meaning: "",
          unit: ""
        }
      },
      // 
      standard_state: 0,
      // 
      currentpage: 1, // 
      pagesize: 10 // 
    };
  },
  computed: {
    train_mode: { // 
      get() {
        return this.$store.state.train_mode;
      },
      set(newVal) {
        this.$store.commit('set_train_mode', newVal);
      }
    },
    train_data_method: { // 
      get() {
        return this.$store.state.train_data_method;
      },
      set(newVal) {
        this.$store.commit('set_train_data_method', newVal);
      }
    },
    train_data_input: { // 
      get() {
        return this.$store.state.train_data_input;
      },
      set(newVal) {
        this.$store.commit('set_train_data_input', newVal);
      }
    },
    train_data() { // 
      return require(`@/assets/data/mode_${this.train_mode}.json`);
    },
    process_input_data: { // 
      get() {
        return this.$store.state.process_input_data;
      },
      set(newVal) {
        this.$store.commit('set_process_input_data', newVal);
      }
    },
    process_setting: { // 
      get() {
        return this.$store.state.process_setting;
      },
      set(newVal) {
        this.$store.commit('set_process_setting', newVal);
      }
    },
    process_result: { // 
      get() {
        return this.$store.state.process_result;
      },
      set(newVal) {
        this.$store.commit('set_process_result', newVal);
      }
    },
    train_process_result: { // 
      get() {
        return this.$store.state.train_process_result;
      },
      set(newVal) {
        this.$store.commit('set_train_process_result', newVal);
      }
    },
  },
  watch: {
    // process_setting
    process_setting: {
      handler(newVal, oldVal) {
        this.process_setting = newVal;
      },
      deep: true,
      immediate: true
    },
  },
  methods: {
    // 
    async process() {
      this.standard_state = this.process_setting.standard.state;
      // 
      if (this.process_input_data.length > 0) {
        // 
        let train_data = [];
        if (this.train_data_method == 1) {
          train_data = await axios.get(`/${this.train_mode}.csv`).then(res => {
            return d3.csvParse(res.data);
          });
        } else {
          if (this.train_data_input != []) {
            train_data = this.train_data_input;
          } else {
            this.$message({
              showClose: true,
              message: '',
              type: 'warning'
            });
          }
        }
        // 
        this.process_setting.data = this.process_input_data;
        process_user({ train_data, ...this.process_setting }).then(result => {
          this.train_process_result = JSON.parse(result.inside);
          this.process_result = JSON.parse(result.user);
        });
      } else {
        this.$message({
          showClose: true,
          message: '',
          type: 'warning'
        });
      }
    },
    // 
    switch_columns() {
      switch (this.train_mode) {
        case 1:
          this.columns = ["SPO", "BMPO", "BFPO", "SPE", "BMPE", "BFPE", "ST", "BT", "WP", "FN", "SFR", "BFR"];
          break;
        case 2:
          this.columns = ["SPO", "BMPO", "BFPO", "SPE", "BMPE", "BFPE", "ST", "BT", "WP", "FN", "SD", "SFR", "BFR"];
          break;
        case 3:
          this.columns = ["SPO", "BMPO", "BFPO", "SPE", "BMPE", "BFPE", "ST", "BT", "WP", "FN", "SD", "SFR", "BFR"];
          break;
        case 4:
          this.columns = ["SPO", "BMPO", "BFPO", "SPE", "BMPE", "BFPE", "ST", "BT", "WP", "FN", "SD", "SFR", "BFR"];
          break;
        case 5:
          this.columns = ["SPO", "BMPO", "BFPO", "SPE", "BMPE", "BFPE", "ST", "BT", "WP", "FN", "FP", "SFR", "BFR"];
          break;
        default:
          this.columns = ["SPO", "BMPO", "BFPO", "SPE", "BMPE", "BFPE", "ST", "BT", "WP", "FN", "SFR", "BFR"];
      }
    },
    // 
    handleSizeChange(val) {
      this.currentpage = 1;
      this.pagesize = val;
    },
    // 
    handleCurrentChange(val) {
      this.currentpage = val;
    },
    // 
    table_index(index) {
      // indexindex +1
      this.currentpage <= 0 ? this.currentpage = 1 : this.currentpage;
      // 
      if (this.currentpage != 1) {
        // index + 1 + ((  - 1) * )
        //  1 + ((2 - 1)*5) = 6,6
        return index + 1 + ((this.currentpage - 1) * this.pagesize);
      }
      // +1
      return index + 1;
    }
  },
  mounted() {
    // 
    this.switch_columns();
  }
};
</script>

<style lang="scss" scoped>
#process-result {

  // card
  &>* {
    margin-bottom: 20px;
  }

  .setting {
    display: flex;
    flex-direction: row;
  }

  // 
  ::v-deep(.el-table__cell) {
    padding: 0;
  }
}
</style>