<template>
  <div id="process-standard">
    <!--  -->
    <el-card shadow="hover">
      <div class="setting">
        <div class="setting-method" style="margin-right: 20px;">
          <span></span>
          <el-select v-model="process_setting.standard[0].state" placeholder="">
            <el-option v-for="state in process_standard_states" :key="state.value" :label="state.label"
              :value="state.value">
            </el-option>
          </el-select>
        </div>
        <div class="setting-method" style="margin-right: 20px;">
          <span></span>
          <el-select v-model="process_setting.standard[0].method" placeholder=""
            :disabled="!process_setting.standard[0].state">
            <el-option v-for="method in process_standard_methods" :key="method.value" :label="method.label"
              :value="method.value">
            </el-option>
          </el-select>
        </div>
      </div>
    </el-card>
    <!--  -->
    <el-card class="info" shadow="hover">
      <div slot="header">
        <b>
          
        </b>
      </div>
      <p><b>MinMaxScaler</b>[0,1]</p>
      <p><b>StandardScaler</b></p>
      <p><b>MaxAbsScaler</b>[0,1][-1,0][-1,1]</p>
      <p><b>RobustScaler</b>StandardScaler</p>
    </el-card>
  </div>
</template>

<script>
export default {
  name: 'ProcessStandard',
  data() {
    return {
      // 
      process_standard_states: [
        {
          value: 0,
          label: ''
        }, {
          value: 1,
          label: ''
        }
      ],
      // 
      process_standard_methods: [
        {
          value: 1,
          label: 'MinMaxScaler'
        }, {
          value: 2,
          label: 'StandardScaler'
        }, {
          value: 3,
          label: 'MaxAbsScaler'
        }, {
          value: 4,
          label: 'RobustScaler'
        }
        // , {
        //   value: 5,
        //   label: 'Normalizer'
        // }
      ],
    };
  },
  computed: {
    process_input_data: { // 
      get() {
        return this.$store.state.process_input_data;
      },
      set(newVal) {
        this.$store.commit('set_process_input_data', newVal);
      }
    },
    process_setting: { // 
      get() {
        return this.$store.state.process_setting;
      },
      set(newVal) {
        this.$store.commit('set_process_setting', newVal);
      }
    },
    process_result: { // 
      get() {
        return this.$store.state.process_result;
      },
      set(newVal) {
        this.$store.commit('set_process_result', newVal);
      }
    },
    train_process_result: { // 
      get() {
        return this.$store.state.train_process_result;
      },
      set(newVal) {
        this.$store.commit('set_train_process_result', newVal);
      }
    },
  },
  watch: {
    // process_setting
    process_setting: {
      handler(newVal, oldVal) {
        this.process_setting = newVal;
        // 
        this.process_result = [];
        this.train_process_result = [];
      },
      deep: true,
      immediate: true
    },
  },
  methods: {
  },
  mounted() {
  }
};
</script>

<style lang="scss" scoped>
#process-standard {

  // card
  &>* {
    margin-bottom: 20px;
  }

  .setting {
    display: flex;
    flex-direction: row;
  }
}
</style>