<template>
  <div id="process-input">
    <el-card v-if="process_input_method == 1" class="form-example" shadow="hover">
      <div slot="header">
        <b>
          
        </b>
      </div>
      <el-table :data="data_example" style="width: 100%" stripe>
        <!-- <el-table-column type="index" width="50" fixed>
        </el-table-column> -->
        <!--  -->
        <el-table-column type="index" width="50" fixed>
        </el-table-column>
        <!--  -->
        <el-table-column v-for="column in columns" :key="column" :prop="column" :label="column">
        </el-table-column>
      </el-table>
    </el-card>
    <el-card v-if="process_input_method == 1" class="form-upload" shadow="hover">
      <div slot="header">
        <el-upload class="upload-file" ref="upload" action="/" :show-file-list="false" :on-change="upload_excel"
          accept=".csv,.xlsx,.xls" :auto-upload="false">
          <el-button icon="el-icon-upload" size="small" type="primary">
            
          </el-button>
        </el-upload>
      </div>
      <el-table v-if="process_input_data.length > 0"
        :data="process_input_data.slice((currentpage - 1) * pagesize, currentpage * pagesize)" style="width: 100%"
        :height="250" stripe>
        <el-table-column type="index" :index="table_index" width="50" fixed>
        </el-table-column>
        <el-table-column v-for="column in columns" :key="column" :prop="column" :label="column">
        </el-table-column>
      </el-table>
      <!--  -->
      <div>
        <el-pagination v-if="process_input_data.length > 0" align='center' @size-change="handleSizeChange"
          @current-change="handleCurrentChange" :current-page="currentpage" :page-sizes="[5, 10, 50, 100]"
          :page-size="pagesize" layout="total, sizes, prev, pager, next, jumper" :total="process_input_data.length">
        </el-pagination>
      </div>
    </el-card>
    <el-card v-if="process_input_method == 2" class="form-online" shadow="hover">
      online input
    </el-card>
  </div>
</template>

<script>
import * as XLSX from 'xlsx';
import * as d3 from "d3-dsv";
import axios from "axios";

export default {
  name: 'ProcessInput',
  data() {
    return {
      // 
      process_input_methods: [{
        value: 1,
        label: ''
      }, {
        value: 2,
        label: ''
      }],
      // 
      data_example: [],
      // 
      columns: ["SPO", "BMPO", "BFPO", "SPE", "BMPE", "BFPE", "ST", "BT", "WP", "FN", "SFR", "BFR"],
      // 
      currentpage: 1, // 
      pagesize: 10 // 
    };
  },
  computed: {
    train_mode: { // 
      get() {
        return this.$store.state.train_mode;
      },
      set(newVal) {
        this.$store.commit('set_train_mode', newVal);
      }
    },
    process_input_method: { // 
      get() {
        return this.$store.state.process_input_method;
      },
      set(newVal) {
        this.$store.commit('set_process_input_method', newVal);
      }
    },
    process_input_data: { // 
      get() {
        return this.$store.state.process_input_data;
      },
      set(newVal) {
        this.$store.commit('set_process_input_data', newVal);
      }
    },
  },
  watch: {
    train_mode: function (val) {
      // 
      this.switch_columns();
      // 
      this.set_data_example();
    }
  },
  methods: {
    // 
    set_data_example() {
      axios.get(`/${this.train_mode}_example.csv`).then(res => {
        this.data_example = d3.csvParse(res.data);
      });
    },
    // 
    switch_columns() {
      switch (this.train_mode) {
        case 1:
          this.columns = ["SPO", "BMPO", "BFPO", "SPE", "BMPE", "BFPE", "ST", "BT", "WP", "FN", "SFR", "BFR"];
          break;
        case 2:
          this.columns = ["SPO", "BMPO", "BFPO", "SPE", "BMPE", "BFPE", "ST", "BT", "WP", "FN", "SD", "SFR", "BFR"];
          break;
        case 3:
          this.columns = ["SPO", "BMPO", "BFPO", "SPE", "BMPE", "BFPE", "ST", "BT", "WP", "FN", "SD", "SFR", "BFR"];
          break;
        case 4:
          this.columns = ["SPO", "BMPO", "BFPO", "SPE", "BMPE", "BFPE", "ST", "BT", "WP", "FN", "SD", "SFR", "BFR"];
          break;
        case 5:
          this.columns = ["SPO", "BMPO", "BFPO", "SPE", "BMPE", "BFPE", "ST", "BT", "WP", "FN", "FP", "SFR", "BFR"];
          break;
        default:
          this.columns = ["SPO", "BMPO", "BFPO", "SPE", "BMPE", "BFPE", "ST", "BT", "WP", "FN", "SFR", "BFR"];
      }
    },
    // 
    upload_excel(file) {
      new Promise(function (resolve, reject) {
        const reader = new FileReader();
        reader.onload = function (e) {
          const data = e.target.result;
          this.wb = XLSX.read(data, {
            type: 'binary'
          });
          const result = [];
          this.wb.SheetNames.forEach((sheetName) => {
            result.push({
              sheetName: sheetName,
              columns: XLSX.utils.sheet_to_json(this.wb.Sheets[sheetName], { header: 1 })[0],
              sheet: XLSX.utils.sheet_to_json(this.wb.Sheets[sheetName]),
            });
          });
          resolve(result);
        };
        reader.readAsBinaryString(file.raw);
      }).then(tabJson => {
        if (tabJson && tabJson.length > 0) {
          // store
          // this.process_upload_sheetname = tabJson[0].sheetName; // sheetname
          // this.process_upload_columns = tabJson[0].columns; // 
          this.process_input_data = tabJson[0].sheet; //
        }
      });
    },
    // 
    // 
    handleSizeChange(val) {
      this.currentpage = 1;
      this.pagesize = val;
    },
    // 
    handleCurrentChange(val) {
      this.currentpage = val;
    },
    // 
    table_index(index) {
      // indexindex +1
      this.currentpage <= 0 ? this.currentpage = 1 : this.currentpage;
      // 
      if (this.currentpage != 1) {
        // index + 1 + ((  - 1) * )
        //  1 + ((2 - 1)*5) = 6,6
        return index + 1 + ((this.currentpage - 1) * this.pagesize);
      }
      // +1
      return index + 1;
    }
  },
  mounted() {
    // 
    this.switch_columns();
    // 
    this.set_data_example();
  }
};
</script>

<style lang="scss" scoped>
#process-input {

  // card
  &>* {
    margin-bottom: 20px;
  }

  // 
  ::v-deep(.el-table__cell) {
    padding: 0;
  }
}
</style>
