<template>
  <div id="train-algorithm-xgb">
    <el-card shadow="hover">
      <div slot="header">
        <b></b>
      </div>
      <div class="setting">
        <!--  -->
        <div class="setting-method" style="margin-right: 20px;">
          <span></span>
          <el-input-number v-model="train_algorithm_xgb_setting.test_size" :min="0.05" :max="0.3"
            :step="0.01"></el-input-number>
        </div>
        <!-- learning_rate -->
        <div class="setting-method" style="margin-right: 20px;">
          <span></span>
          <el-input-number v-model="train_algorithm_xgb_setting.paras[0].learning_rate" :min="0.1"
            :step="0.1"></el-input-number>
        </div>
        <!-- max_depth -->
        <div class="setting-method" style="margin-right: 20px;">
          <span></span>
          <el-input-number v-model="train_algorithm_xgb_setting.paras[0].max_depth" :min="1" :step="1"></el-input-number>
        </div>
        <!-- n_estimators -->
        <div class="setting-method" style="margin-right: 20px;">
          <span></span>
          <el-input-number v-model="train_algorithm_xgb_setting.paras[0].n_estimators" :min="1"
            :step="1"></el-input-number>
        </div>
      </div>
    </el-card>
    <el-card class="info" shadow="hover">
      <div slot="header">
        <b></b>
      </div>
      <p><b></b>0.050.3</p>
      <p><b></b>0.1</p>
      <p><b></b>1</p>
      <p><b></b>0</p>
    </el-card>
    <el-card class="info" shadow="hover">
      <div slot="header">
        <b></b>
      </div>
      <p>
        XGBoostGBDTXGBoost
      </p>
      <p>
        <Latex>{{ formula_1 }}</Latex>
      </p>
      <p>
        <Latex>{{ formula_2 }}</Latex>loss
      </p>
      <p>
        <Latex>\Omega\left( f_{t} \right)</Latex>
      </p>
      <p>
        <Latex>{{ formula_3 }}</Latex>
      </p>
      <p>
        <Latex>\Omega(f) = \gamma T + \frac{1}{2}\lambda\left\| \omega \right\|^{2}</Latex>
      </p>
      <p>
        T
      </p>
      <p>
        
      </p>
      <p>
        XGBoostsloss
      </p>
    </el-card>
    <el-card class="form-upload" shadow="hover">
      <div slot="header">
        <div class="model-tool">
          <el-button type="primary" @click="train_model"></el-button>
          <el-button type="primary" @click="save"></el-button>
        </div>
      </div>
      <div v-show="Object.keys(train_algorithm_xgb_result).length > 0" class="model-result">
        <!--  -->
        <b>
          
        </b>
        <el-table v-if="Object.keys(train_algorithm_xgb_result).length > 0" :data="[train_algorithm_xgb_result.f1.error]"
          style="width: 100%;margin-bottom: 30px;" stripe>
          <el-table-column v-for="column in error_columns" :key="column" :prop="column" :label="column">
          </el-table-column>
        </el-table>
        <b>
          
        </b>
        <el-table v-if="Object.keys(train_algorithm_xgb_result).length > 0" :data="[train_algorithm_xgb_result.f2.error]"
          style="width: 100%;margin-bottom: 30px;" stripe>
          <el-table-column v-for="column in error_columns" :key="column" :prop="column" :label="column">
          </el-table-column>
        </el-table>
        <!--  -->
        <div id="lr-f1-lineplot"></div>
        <div id="lr-f2-lineplot"></div>
        <div id="lr-g-lineplot"></div>
      </div>
    </el-card>
  </div>
</template>

<script>
// import * as XLSX from 'xlsx';
import { train } from "@/api";
import { draw_lineplot } from "@/utils/draw";
import Latex from "@/components/Latex.vue";


export default {
  name: 'TrainAlgorithmXgb',
  components: {
    Latex
  },
  data() {
    return {
      // {{}}vue\
      formula_1: "L(\\phi) = {\\sum_{i}{l\\left( {{\\hat{y}}_{i},y_{i}} \\right)}} + {\\sum_{k}\\Omega}\\left( f_{t} \\right)",
      formula_2: "l\\left( {{\\hat{y}}_{i},y_{i}} \\right)",
      formula_3: "{\\hat{y}}_{i}",
      // 
      error_columns: ["MAE", "RMSE", "R2"],
    };
  },
  computed: {
    train_process_result: { // 
      get() {
        return this.$store.state.train_process_result;
      },
      set(newVal) {
        this.$store.commit('set_train_process_result', newVal);
      }
    },
    train_algorithm_xgb_setting: { // XGBoost
      get() {
        return this.$store.state.train_algorithm_xgb_setting;
      },
      set(newVal) {
        this.$store.commit('set_train_algorithm_xgb_setting', newVal);
      }
    },
    train_algorithm_xgb_result: { // XGBoost
      get() {
        return this.$store.state.train_algorithm_xgb_result;
      },
      set(newVal) {
        this.$store.commit('set_train_algorithm_xgb_result', newVal);
      }
    },
    train_save: { // 
      get() {
        return this.$store.state.train_save;
      },
      set(newVal) {
        this.$store.commit('set_train_save', newVal);
      }
    },
  },
  watch: {
    // train_algorithm_xgb_setting
    train_algorithm_xgb_setting: {
      handler(newVal, oldVal) {
        this.train_algorithm_xgb_setting = newVal;
      },
      deep: true,
      immediate: true
    },
  },
  methods: {
    // 
    train_model() {
      // 
      if (this.train_process_result.length > 0) {
        // 
        this.train_algorithm_xgb_setting.data = this.train_process_result;
        train(this.train_algorithm_xgb_setting).then(result => {
          this.train_algorithm_xgb_result = result;
          // 
          this.draw_lineplot_all();
        });
      } else {
        this.$message({
          showClose: true,
          message: '',
          type: 'error'
        });
      }
    },
    // 
    save() {
      // 
      if (Object.keys(this.train_algorithm_xgb_result).length > 0) {
        const train_algorithm_xgb_setting = this.train_algorithm_xgb_setting;
        const train_algorithm_xgb_result = this.train_algorithm_xgb_result;
        this.train_save = {
          train_algorithm_setting: train_algorithm_xgb_setting,
          train_algorithm_result: train_algorithm_xgb_result
        };
        // 
        this.$message({
          message: '',
          type: 'success'
        });
      } else {
        this.$message({
          showClose: true,
          message: '',
          type: 'error'
        });
      }
    },
    // 
    draw_lineplot_all() {
      // 
      draw_lineplot("lr-f1-lineplot", this.train_algorithm_xgb_result, "f1");
      draw_lineplot("lr-f2-lineplot", this.train_algorithm_xgb_result, "f2");
      // draw_lineplot("lr-g-lineplot", this.train_algorithm_xgb_result, "g");
    },
  },
  mounted() {
    // 
    if (Object.keys(this.train_algorithm_xgb_result).length > 0) {
      this.draw_lineplot_all();
    }
  }
};
</script>

<style lang="scss" scoped>
#train-algorithm-xgb {

  // card
  &>* {
    margin-bottom: 20px;
  }

  .setting {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;

    .setting-method {
      margin-bottom: 20px;
    }
  }

  // .model-tool {
  //   display: flex;
  //   flex-direction: row;
  //   justify-content: space-between;
  //   align-items: center;
  // }
}
</style>
